.speech_container {
	height: calc(100% - (38px + 1rem));
	.speech_value_container {
		border: 1px solid var(--formBorder);
		padding: 15px;
		border-radius: 6px;
		height: calc(100% - 131px);
		position: relative;
		textarea {
			width: 100%;
			border: none !important;
			outline: none !important;
			box-shadow: none !important;
			padding: 0 0 0 0;
			height: calc(100% - 70px);
			margin-top: 0;
			margin-bottom: 10px;
			resize: none;
		}
	}
}
.delete_icon {
	color: var(--invalid);
	font-size: 20px;
}
.send_icon {
	color: var(--themeColor);
	font-size: 20px;
}
.stop_icon {
	color: var(--invalid);
	font-size: 40px;
}
