.main_dashboard {
	height: 100%;
	.main_dashboard_welcome {
		background-image: url("../../../public//images/farm-bg.jpg");
		background-position: center;
		background-size: cover;
		background-repeat: no-repeat;
		color: var(--white);
		padding: 15px;
		padding-bottom: 30px;
		border-radius: 6px;
		overflow: hidden;
		min-height: 200px;
		.main_dashboard_welcome_head {
			font-size: 16px;
			margin-bottom: 10px;
		}
		.main_dashboard_welcome_text {
			word-break: break-all;
			word-break: break-word;
			white-space: pre-wrap;
		}
	}
	.solution_container {
		margin-top: 20px;
		.solution_head {
			font-size: 18px;
			color: var(--labelColor);
			margin-bottom: 15px;
		}
		.solution_content {
			display: flex;
			justify-content: center;
			flex-wrap: wrap;
			gap: 15px;
			.solution_item {
				flex: 1 1 10px;
				width: 10px;
				padding: 30px 20px;
				display: flex;
				align-items: center;
				justify-content: center;
				border-radius: 7px;
				background-color: var(--mainBg);
				max-width: 300px;
				min-width: 150px;
				cursor: pointer;
				.solution_item_icon {
					font-size: 40px;
					color: var(--themeColor);
				}
				svg {
					&.solution_item_icon {
						width: 30px;
						fill: var(--themeColor);
					}
				}
			}
		}
	}
}
