@font-face {
	font-family: "regular";
	src: url("./fonts/noto-sans/noto-sans-jp-japanese-400.woff") format("woff");
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: "bold";
	src: url("./fonts/noto-sans/noto-sans-jp-japanese-900.woff") format("woff");
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: "black";
	src: url("./fonts/noto-sans/noto-sans-jp-japanese-700.woff") format("woff");
	font-weight: normal;
	font-style: normal;
}
@font-face {
	font-family: "light";
	src: url("./fonts/noto-sans/noto-sans-jp-japanese-300.woff") format("woff");
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: "medium";
	src: url("./fonts/noto-sans/noto-sans-jp-japanese-500.woff") format("woff");
	font-weight: normal;
	font-style: normal;
}
html {
	body {
		font-size: 14px;
		font-family: "regular";
		.font_body {
			font-size: 14px;
		}
		.font_11 {
			font-size: 11px;
		}
		.font_12 {
			font-size: 12px;
		}
		.font_13 {
			font-size: 13px;
		}
		.font-16 {
			font-size: 16px;
		}
		.font_20 {
			font-size: 20px;
		}
		.font_weight_bold {
			font-family: "bold";
		}
		.font_weight_black {
			font-family: "black";
		}
		.font_weight_medium {
			font-family: "medium";
		}
		.font_weight_thin {
			font-family: "light";
		}
		.font_weight_regular {
			font-family: "regular";
		}
	}
}
