.image_drag_container {
	height: calc(100% - 113px);
	border: 1px solid var(--formBorder);
	border-top: none;
	border-radius: 0 0 4px 4px;
	padding: 15px;
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	> input[type="file"] {
		box-shadow: none !important;
		border: none !important;
		outline: none !important;
		opacity: 0;
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		cursor: pointer;
		&:disabled,
		&[disabled] {
			cursor: not-allowed;
		}
	}
	.imported_item_container {
		height: 100%;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		img {
			max-height: 100%;
			max-width: 100%;
			object-fit: contain;
		}
	}
}
.image_browse_tab_container {
	height: calc(100% - (61px + 1rem));
	ul {
		display: flex;
		padding-left: 0;
		margin: 0 0 0 0;
		list-style: none;
		border-bottom: 1px solid var(--formBorder);
		width: 100%;
		li {
			padding: 10px 20px;
			margin-right: 3px;
			cursor: pointer;
			border: 1px solid var(--formBorder);
			border-bottom: none;
			border-radius: 4px 3px 0px 0px;
			&.active_tab {
				background-color: var(--themeColor);
				border-color: var(--themeColor);
				a {
					color: var(--white) !important;
				}
			}
			a {
				color: var(--textColor) !important;
			}
		}
	}
}

.capture_modal {
	width: 95% !important;
	max-width: 1000px;
	top: auto !important;
	transform: none !important;
	padding-bottom: 0 !important;
	.image_capture_container {
		height: calc(100vh - 295px) !important;
		padding-bottom: 0;
	}
	.ant-modal-close-x {
		display: none;
	}
}

.ant-modal-wrap {
	display: flex;
	align-items: center;
	justify-content: center;
}
